import { memo } from 'react';
import dynamic from 'next/dynamic';
import { I_Scene } from '@containers/Home/types';
import { ThemeCustom1Wrapper } from './styled';
import { useSelector } from 'react-redux';
import { selectIsMatterportProject } from '@containers/Home/selectors';

const PostScene = dynamic(() => import('./PostScene'));
const Controlbar = dynamic(() => import('./Controlbar'));
const ListScene = dynamic(() => import('./ListScene'), { ssr: false });
// const Cursor = dynamic(() => import('./Cursor'), { ssr: false });
const Tour = dynamic(() => import('@containers/Home/components/Tour'), {
  ssr: false,
});

interface Props {
  onNextScene: () => void;
  onChangeScene: (scene: I_Scene) => void;
}

export const ThemeCustom1 = memo(({ onChangeScene, onNextScene }: Props) => {
  const isMatterportProject = useSelector(selectIsMatterportProject);

  return (
    <ThemeCustom1Wrapper className={isMatterportProject ? 'matterport' : ''}>
      <ListScene onChangeScene={onChangeScene} />

      <Tour onChangeScene={onChangeScene} onNextScene={onNextScene}>
        {({ krpanoRef }) => <Controlbar krpanoRef={krpanoRef} />}
      </Tour>

      <PostScene />
    </ThemeCustom1Wrapper>
  );
});

export default ThemeCustom1;
